.h1 {
	@include hidetext; }
.first-screen {
	padding-top: 48px;
	@include image("fs-bg-day.jpg");
	height: 100vh;
	@include r(1200) {
		min-height: 800px;
		height: 100%; }
	@include r(991) {
		min-height: 650px; }
	@include r(767) {
		min-height: 300px; }
	@include cover; }
.is-night-js {
	.first-screen {
		@include image("fs-bg-night.jpg"); } }
.switch {
	text-align: center;
	@include r(767) {
		display: inline-block;
		position: absolute;
		right: 15px;
		top: 15px; } }
.switch__month, .switch__sun {
	display: inline-block;
	position: relative; }
.is-night-js {
	.switch__month {
		@include s(n-month);
		&:after {
			background: #fff; } }
	.switch__sun {
		@include s(n-sun); } }
.switch__month {
	margin-right: 32px;
	@include s(d-month);
	&:after {
		content: '';
		position: absolute;
		right: -17px;
		top: 50%;
		transform: translateY(-50%);
		width: 2px;
		height: 54px;
		background: #2e2e2e; }
	&:hover {
		@include s(month-is-hover); } }
.switch__sun {
	@include s(d-sun);
	&:hover {
		@include s(sun-is-hover); } }
.first-screen__phone {
	color: $black;
	position: absolute;
	right: 87px;
	top: 13px;
	@include r(991) {
		right: 0px;
		font-size: 22px; }
	@include r(767) {
		top: -8px;
		right: 15px; } }
.is-night-js {
	.first-screen__phone {
		color: $white; } }
.logo {
	@include image("d-logo.png");
	width: 210px;
	height: 368px;
	position: relative;
	top: -15px;
	@include cover;
	display: inline-block;
	@include r(991) {
		width: 150px;
		height: 250px;
		background-size: contain; }
	@include r(767) {
		width: 100px;
		height: 180px;
		top: -40px; } }

.is-night-js {
	.logo {
		@include image("n-logo.png"); } }
.menu-btn {
	position: fixed;
	right: 15px;
	top: 10px;
	width: 30px;
	height: 20px;
	overflow: visible;
	z-index: 22;
	@include rmin(768) {
		display: none; }
	div {
		height: 3px;
		width: 30px;
		z-index: 2;
		background: $black;
		position: absolute;
		top: 50%;
		margin-top: -2px;
		transition: all .3s;
		&:before, &:after {
			transition: all .3s;
			content: '';
			position: absolute;
			height: 3px;
			width: 30px;
			background: inherit; }
		&:before {
			top: -8px;
			transform-origin: top right; }
		&:after {
			bottom: -9px;
			transform-origin: bottom right; } } }
.is-night-js {
	.menu-btn {
		div {
			background: $blue-bg; } } }

.btn-is-active {
	div {
		height: 2px;
		@include rotate(90);
		&:before, &:after {
			width: 50%;
			height: 2px; }
		&:before {
			transform: translateX(16px) rotate(45deg);
			top: 1px; }
		&:after {
			transform: translateX(16px) rotate(-45deg);
			bottom: 1px; } } }
